                                li {font-size:em(26); line-height:em(40); margin:0; color:$gray6;}
                                em {padding:0; display:block;}
                            }
                            .ordered_history {display:block; font-size:em(26); line-height:em(40); color:$gray6;}
                            .order_total_price_m {display:block; font-size:em(32); color:$gray7; font-weight:$bold; padding:em(10) 0 0}
                        }
                    }
                    .btn_wrap {
                        width:100%; padding:em(32) 0 0; text-align:center;
                        a, button {
                            width:calc(50% - .3rem); padding:0; border:em(2) solid $gray3; border-radius:em(36); margin:0;
                            &:hover {
                                span {color:$gray7;}
                            }
                            &.reorder {margin:0;}
                        }
                        a:nth-of-type(2), button {float:right;}
                        a:nth-of-type(3), button {margin:em(14) 0 0;}
                        span {font-size:em(24); font-weight:$bold; line-height:em(68);}
                    }
                    .option_list {
                        width:100%; margin:em(36) 0 0; padding:em(26) 0 0; border-top:em(2) solid #e5e5e5;
                        > li {
                            margin:em(2) 0 0; padding:0; border-bottom:none;
                            &:first-child {margin:0; border-top:none;}
                            > span {font-size:em(28); font-weight:$bold; top:em(18); right:em(80);}
                        }
                        button {
                            font-size:em(28); padding:em(18) 0;
                            &:after {
                                width:em(26); height:em(16); background-image:url(../../images/my_menu/order_more_btn_m.png); background-size:em(26) em(16);
                            }
                        }
                        .detail_option {
                            margin:0 0 em(30); padding:0; background-color:$white; border:em(2) solid $gray2;
                            .first_option {
                                margin:0;
                                .product_area {border-top:none;}
                            }
                            .second_option {
                                margin:0; padding:em(80) 0 0; border-top:em(2) solid $gray2;
                                .product_area {padding:em(60); border-top:none;}
                                .product_info_area {
                                    padding:em(60); border-top:none;
                                    span {padding:0;}
                                }
                            }
                            p {font-size:em(32); float:left;}
                            .product_area {
                                width:100%; padding:em(60); box-sizing:border-box;
                                p {width:calc(50% - 4px);}
                                ul {width:calc(50% - 4px); padding:0; float:right;}
                                li {margin:em(12) 0 0; padding:0 0 0 em(24); font-size:em(28);}
                            }
                            .product_info_area {
                                width:100%; margin:em(72) 0 0; padding:em(60); box-sizing:border-box; background-color:$gray1;
                                > div {width:100%; margin:em(30) 0 0;}
                                .material_code {margin:0;}
                                span {font-size:em(28); padding:0; float:right;}
                            }
                            .discount_option {
                                margin:0; padding:em(56); border-top:em(2) solid $gray2; background-color:$gray1; text-align:left;
                                p {
                                    width:100%; margin:em(30) 0 0; font-size:em(28); float:none; position:relative;
                                    &:first-child {margin:0;}
                                    span {position:absolute; top:0; right:0;}
                                }
                            }
                        }
                        .list_form {
                            padding:em(60);
                            dl {margin:em(30) 0 0;}
                            dt {width:calc(50% - 4px); font-size:em(32);}
                            dd {width:calc(50% - 4px); font-size:em(32); float:right;}
                        }
                    }
                }
            }
        }
    }
}
// NHA-COMMON-ITEM_LIST-026_ATM(T)
.block_list_type {
    @extend %clearfix;
    .type_link_btn {
        padding:30px 0 0; float:right;
        @extend %clearfix;
        @include boxSizing;
        a {
            float:left; display:block; height:38px;padding:0 20px;font-size:13px;font-weight:$medium;color:$gray7;
            border:1px solid $gray3; vertical-align:middle; line-height:36px; margin:0 0 0 10px;
            @include borderRadius(100px);
            &:first-of-type {margin:0;}
        }
    }
    .tab_wrap {
        width:100%; border-bottom:1px solid $gray2; clear:both; padding:35px 0 0;
        @include boxSizing;
        ul {
            width:fit-content; margin:0 auto;
            @extend %clearfix;
        }
        li {margin:0 20px;float:left;}
        button {
            display:block;padding:15px 30px;font-size:18px;color:$gray5;font-weight:$regular; position:relative;
            @include transition(all, .2s);
            &:after {
                width:100%; height:2px; background-color:$blue5;content:"";display:block;
                position:absolute; bottom:-1px;left:0; opacity:0;
                @include transition(all, .2s);
            }
            &:hover, &:focus {
                font-weight:$medium; color:$gray7;
                &:after {opacity:1;}
            }
            &.active {
                font-weight:$medium; color:$gray7;
                &:after {opacity:1;}
            }
        }
    }
    .item_list {
        padding:50px 0 200px; display:none;
        @include boxSizing;
        &:first-of-type { display:block; }
        li {
            border:1px solid $gray2; overflow:hidden; margin:30px 0 0;
            @include borderRadius(15px);
            &:first-of-type { margin:0; }
        }
        .number_info {
            width:100%; padding:23px 30px 20px 30px; background-color:$gray1;
            @include boxSizing;
            @extend %clearfix;
            .form_check_radio_default {
                width:22px; height:22px; float:left; margin:4px 30px 0 0;
                input[type="checkbox"] + label {
                    position:absolute; top:0; left:0;
                    &:before {border-color:$gray3;}
                }
            }
            dl {
                margin:0 0 0 40px;
                float:left;@extend %clearfix;
                &:first-of-type {margin:0;}
            }
            dt {float:left;font-size:14px;font-weight:$regular;color:$gray5;line-height:32px;}
            dd {float:left;font-size:18px;color:$gray7;font-weight:$medium;line-height:28px; padding:0 0 0 6px;}
        }
        .content_wrap {
            padding:30px 80px 20px 80px;
            @include boxSizing;
            @extend %clearfix;
            .info_txt_wrap {
                display:flex;flex-wrap:wrap;
                @include boxSizing;
                dl {
                    padding:0 15px 0 0;
                    @include boxSizing;
                    &:nth-of-type(3n+1) {width:280px;}
                    &:nth-of-type(3n+2) {width:310px;}
                    &:nth-of-type(3n+3) {width:310px;}
                    &:nth-of-type(n+4) { margin:22px 0 0; }
                }
                dt {font-size:14px;font-weight:$regular;color:$gray5;}
                dd {padding:2px 0 0;font-size:16px;font-weight:$regular;color:$gray7;line-height:26px;}
            }
            .warranty_wrap {
                width:130px; float:right; padding:38px 0 0;
                .kind {
                    margin:0 auto; font-size:12px; display:block; padding:5px 12px; font-weight:$medium;
                    @include borderRadius(10px);
                    &.in_warranty {color:$blue5;background-color:#dcebfe;}
                    &.out_of_warranty {color:#ea4335;background-color:#fce3e1;}
                }
            }
        }
    }
    // NHA-COMMON-ITEM_LIST-026_PART(T) (두번째 탭)
    .item_list.type02 {
        .content_wrap {
            .info_txt_wrap {
                dl {
                    &:nth-of-type(1) {width:274px;}
                    &:nth-of-type(2) {width:274px;}
                    &:nth-of-type(3) {width:280px;}
                    &:nth-of-type(4) { width:auto; margin:0; }
                }
            }
        }
    }
    // NHA-FINANCIAL-DISPLAY_WARRANTY-009 (세로형 타이틀 형식)
    .item_list.type03 {
        padding:20px 0 200px;
        li { position:relative; }
        .number_info {
            dl {
                margin:0 0 0 90px;
                &:first-of-type { margin:0;}
            }
            dt {float:initial; line-height:18px; font-weight:$medium;}
            dd {float:initial; padding:0; font-size:20px; line-height:30px;}
        }
        .content_wrap {
            height:auto; padding:0 30px; overflow:hidden;
            &.cont_full {
                height:580px;
                .more_wrap { display:block;}
            }
            .row_list_table {
                padding:30px; border-top:1px solid $gray2;
                @include boxSizing;
                &:nth-of-type(1) {border:none;}
            }
            .warranty_wrap {width:120px;}
            .info_txt_wrap {
                dl {
                    padding:0 15px 0 0;
                    @include boxSizing;
                    &:nth-of-type(3n+1) {width:480px;}
                    &:nth-of-type(3n+2) {width:190px;}
                    &:nth-of-type(3n+3) {width:285px;}
                    &:nth-of-type(n+4) { margin:22px 0 0; }
                }
            }
            .more_wrap {
                width:100%;height:180px;position:absolute; bottom:0; left:0; display:none;
                background:linear-gradient(to top, $white 55%, rgba(255, 255, 255, 0) 100%);
                button {
                    display:block;font-size:14px;font-weight:$bold;color:$gray7;line-height:24px;
                    margin:105px auto 0; padding:10px 20px;
                    &:after {
                        width:13px; height:8px;content:"";display:inline-block; margin:0 0 0 5px;
                        background:url(./../../images/common/select_arr_02.png) 50% 50% no-repeat; background-size:cover;
                    }
                }
            }
        }
    }
    // NHA-RETAIL-WARRANTY-005 (체크박스 없는 형식)
    .item_list.type04 {
        padding:40px 0 200px;
        li {
            margin:20px 0 0;
            &:first-of-type { margin:0; }
        }
        .number_info {
            dt { float:initial; line-height:20px; font-weight:$medium; }
            dd { float:initial; padding:0; font-size:20px; }
        }
        .content_wrap {
            padding:30px 60px;
            .warranty_wrap {width:110px;padding:10px 0 0;}
            .info_txt_wrap {
                dl {
                    &:nth-of-type(1) {width:313px;}
                    &:nth-of-type(2) {width:311px;}
                    &:nth-of-type(3) {width:340px;}
                }
            }
        }
    }
    // NHA-COMMON-MY_RMA-033 (버튼 추가 형식)
    .item_list.type05 {
        .content_wrap {
            padding:0;
            .row_list_table {
                padding:30px; border-top:1px solid $gray2;
                @extend %clearfix;
                &:first-of-type { border:none; }
            }
            .info_txt_wrap {
                padding:22px 0 0; float:left;
                @include boxSizing;
                dl {
                    &:nth-of-type(3n+1) {width:initial;}
                    &:nth-of-type(3n+2) {width:initial;}
                    &:nth-of-type(3n+3) {width:initial;}
                    &:nth-of-type(n+4) {width:initial; margin:0;}
                    &:nth-of-type(1) { width:140px; }
                    &:nth-of-type(2) { width:280px; }
                    &:nth-of-type(3) { width:190px; }
                    &:nth-of-type(4) { width:200px; }
                    &.rma_info_elm {
                        dd {
                            & > span {display:block;}
                        }
                    }
                }
            }
            .warranty_wrap {
                width:315px;padding:0;
                @extend %clearfix;
                .rma_state {
                    width:140px;float:left;height:100px;display:flex;justify-content:center;flex-direction:column;
                    @extend %clearfix;
                    .kind {
                        color:$gray5; background-color:#ebebeb;
                        &.on {color:#33a853;background-color:#eaf6ed;}
                    }
                }
                .link_wrap {
                    width:fit-content;height:100px;float:right;display:flex;justify-content:center;flex-direction:column;
                    button {
                        height:38px; font-size:13px;font-weight:$medium;color:$gray7;border:1px solid $gray3; margin:8px 0 0;
                        @include borderRadius(100px);
                        &:first-of-type { margin:0; }
                    }
                }
            }
        }
    }
    @include mob() {
        .type_link_btn {
            float:inherit; padding:em(40) em(30) em(50);
            a {
                width:48.3%;float:left; height:auto;padding:em(22) 0 em(18);font-size:em(24);font-weight:$bold;
                border:em(2) solid $gray3; line-height:em(30); margin:0; text-align:center;
                &:nth-of-type(2) { margin:0 0 0 2%; }
                &:nth-of-type(2n+3) { margin:em(15) 0 0 0; }
                &:nth-of-type(2n+4) { margin:em(15) 0 0 2%; }
            }
        }
        .tab_wrap {
            padding:0; border-bottom:em(2) solid $gray2;
            ul {width:auto; padding:0 em(30);}
            li {
                float:left; margin:0 em(25);
                &:first-of-type { margin:0 em(25) 0 0; }
            }
            button {
                padding:em(30) 0;font-size:em(32);font-weight:$bold;
                &:after {height:em(4);bottom:em(-2);}
                &.active {font-weight:$bold;}
            }
        }
        .item_list {
            padding:em(30) em(30);
            li {
                border:em(2) solid $gray2; margin:em(20) 0 0;
                @include borderRadius(em(20));
            }
            .number_info {
                padding:em(25) em(25) em(23);
                .form_check_radio_default {width:auto; height:auto; float:left;margin:0;}
                dl {
                    width:100%;padding:em(5) 0 0 em(70); float:left; margin:0;
                    @include boxSizing;
                    &:first-of-type {padding:0 0 0 em(70);}
                }
                dt {width:43%;font-size:em(24);font-weight:$bold;line-height:em(28); padding:em(7) 0 0;}
                dd {width:57%;font-size:em(30);font-weight:$bold;line-height:em(40);padding:0;}
            }
            .content_wrap {
                padding:em(40) em(35) em(60);
